@import "_help/_var.sass";

* {
	box-sizing: border-box; }

h1, h2, h3, h4, p, a, input {
	margin: 0;
	padding: 0; }

input, a {
	border: none;
	outline: none; }

.clearfix,
.clearfix::before,
.clearfix::after {
	content: '';
	display: block;
	clear: both; }

.container {
	max-width: 1140px;
	width: 100%;
	margin: 0 auto; }


body {
  font: 14px/1.5 Roboto, Arial, sans-serif;
  width: 100%;
  height: 100%;
  overflow-x: hidden; }

